
import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar, createAnimation } from '@ionic/vue';
import { defineComponent , ref } from 'vue';

export default defineComponent({
  name: 'HomePage',
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar
  },setup(){
    const WeaponCards = ref();
    var Hover = false
      const ShadowAni = createAnimation()
        .duration(150)
        .iterations(1)
        .fromTo("filter", "brightness(0)", "brightness(1)")
      const AnimateHover = createAnimation()
        .duration(150)
        .iterations(1)
        .fromTo("backgroundColor", "#665021", "#c79730")
        
    async function HoverFx(CardId:number){
        var WeaponCard = WeaponCards.value.children[CardId].firstChild.firstChild
        //console.log(WeaponCard)
       
        Hover = !Hover
        
        if(Hover == true){
          AnimateHover.stop()
          AnimateHover.elements.pop()
          AnimateHover.addElement(WeaponCard)
          AnimateHover.direction('normal')
          await AnimateHover.play()
          
        }else{
          AnimateHover.direction('reverse')
          AnimateHover.play()
        }
      }
      return { WeaponCards, HoverFx};
   }, mounted(){
     console.log("Mounted")
  }
});
